* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  height: 300vh;
}

.fixed-wrapper {
  background: orange;
  position: fixed;
  height: 100vh;
  width: 100%;
}

.wrapper{
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;

  .link{
    cursor: pointer;
    transition: scale 0s linear .3s;

    &:hover{
      transform: scale(1.05);
    }
  }
}